<template>
  
  <div>
    <div v-if="imageLoading">
     <v-container>
        <v-row class='justify-center'>
          <v-col 
            :cols=6
            class='text-center mt-4'>
            <v-progress-circular
              indeterminate
              color="black"
            ></v-progress-circular>
          </v-col>
        </v-row>
      </v-container>
    </div>
    <div v-if="image">
      <v-container>
        <v-row class='justify-center'>
          <v-col :cols=3>
            <v-card>
              <v-card-title>Image: '{{image.name}}'</v-card-title>
              <v-card-text>
                <p>Resolution: <b>{{image.resolution.X}}x{{image.resolution.Y}}</b></p>
              </v-card-text>
            </v-card>
          </v-col>

          <v-col 
            :cols=6
            class='text-center'>
            <v-img 
                :src="image.fullBase64" 
                :aspect-ratio="image.resolution.X/image.resolution.Y"
                :max-height="800 < image.resolution.Y ? 800 : image.resolution.Y"
                :max-width="1000 < image.resolution.X ? 1000 : image.resolution.X" alt="Error"
                class='pa-0'/>
          </v-col>
        </v-row>
      </v-container>
            
    </div>
  </div>
</template>

<script>
import imagesApi from '@/api/images';

export default {
  data: () => {
    return {
      image: null,
      imageLoading: false
    };
  },
  async mounted() {
    this.imageLoading = true
    this.image = (await imagesApi.getById(this.$route.params.id)).data
    console.log(this.image.resolution)
    this.imageLoading = false
  },
  methods: {
  }
}
</script>

